*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    overflow: hidden;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

h1{
    font-size: 32px;
    font-weight: 600;
    margin-bottom: 4px;
}

h2{
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 4px;
}

h3{
    font-size: 20px;
    font-weight: 400;
}

p {
    margin: 0;
    font-size: 16px;
    line-height: 140%;
}

.sign {
    position: fixed;
    width: 90%;
    max-width: 480px;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    color:#6a0061;
    background-color: rgba(255, 217, 233, 0.9);
    border-radius: 16px;
    box-sizing: border-box;
    padding: 24px;
    z-index: 999;
    font-family: 'Public Sans', sans-serif;
    display: none;
    text-align: center;
  }


.project {
    position: fixed;
    width: 90%;
    max-width: 480px;
    top: 5%;
    left: 50%;
    padding: 24px;
    transform: translateX(-50%);
    color:#6a0061;
    background-color: rgba(255, 217, 233, 0.9);
    border-radius: 16px;
    z-index: 999;
    font-family: 'Public Sans', sans-serif;
    display: none;
    overflow: hidden;
    box-sizing: border-box;
  }

.content {
    text-align: left;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    margin-bottom: 16px;
}

.text {
    width: 100%;
    display: block;
}

#syng-img {
    background-image: url("/static/img/preview/syng.jpg");
}

#rubiks-img {
    background-image: url("/static/img/preview/rubiks.jpg");
}

#seismic-img {
    background-image: url("/static/img/preview/seismic.jpg");
}

#fabric-img {
    background-image: url("/static/img/preview/fabric.jpg");
}

#tyfa-img {
    background-image: url("/static/img/preview/tyfa.jpg");
}

.thumbnail{
    display: flex;
    width: 80px;
    min-width: 80px;
    height: 80px;
    margin-right: 16px;
    border-radius: 8px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}


button{
    width: 100%;
    padding: 16px;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    border: none;
    background-color: rgb(255, 198, 234);
    color: #aa00ba;
    border-radius: 8px;    
}


#swipe {
    height: 48px;
    width: 48px;
    background-image: url("/static/img/swipe.png");
    background-size: 48px;
    text-align: center;
    margin: auto;
    margin-top: 24px;
}


@media only screen and (max-width: 600px) {
    h1 {
        font-size: 24px;
    }

    h2 {
        font-size: 18px;
    }

    .project {
        padding: 16px;
    }

  }